@import url(https://fonts.googleapis.com/css2?family=Texturina:wght@300;500;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;padding:0;box-sizing:border-box;background-color:#faebd7}.App{font-family:"Texturina",serif;line-height:2rem;font-weight:500;text-align:center}h2,h3,p,ul{margin:0}li{padding:0 .8rem 0 0}h2{font-size:1.2rem;color:green}button{margin:.5rem;padding:1rem;font-size:1rem;background:none;color:green;border:2px solid;border-radius:5px;--hover: #008060;transition:.25s}button:hover,button:focus{border-color:var(--hover);color:#fff;box-shadow:inset 0 -3.4rem 0 0 var(--hover)}button:disabled{border:1px solid #999;background-color:#ccc}.edit-panel{background-color:#faebd7;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;width:100%;position:-webkit-sticky;position:sticky;top:0}.edit-panel button{padding:1rem 2rem}main{width:80%;margin:auto}.character-sheet{border:2px solid gray;border-radius:5px;margin:1rem}header{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;grid-gap:1rem;gap:1rem}header img{width:80%}img{width:4rem}.character-cols{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.character-rows,.stat-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;grid-gap:1rem;gap:1rem}.character-rows .skill-usable,.stat-container .skill-usable{-webkit-flex-basis:70%;flex-basis:70%;padding:0 .8rem}.trisect-flexed{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:1rem;gap:1rem}.input-area{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;grid-gap:1rem;gap:1rem;padding:.5rem;width:50%}.input-area input{width:60%}.input-area h3,.input-area p{-webkit-align-self:center;align-self:center}textarea{width:60%}.wide{width:97%}.black-highlight{color:#fff;background-color:#000}.incorrect-datas p{color:#d80000;font-size:.9rem;margin:0}@media(max-width: 850px){header{margin:1rem;padding:0}header img{width:100%}main{width:100%}}@media(max-width: 600px){.character-rows{-webkit-flex-direction:column;flex-direction:column;grid-gap:0;gap:0}.input-area{width:97%}.input-area input{width:50%}}@media(max-width: 500px){header div h1{font-size:1.6rem}}@media(max-width: 400px){header div h1{font-size:1.4rem}h2{margin:.2rem}.input-area{padding:.4rem}.input-area input{width:40%}.trisect-flexed{grid-gap:0;gap:0}.trisect-flexed h2:nth-of-type(1),.trisect-flexed h3:nth-of-type(1){width:100%}.wide h3{padding:0 1rem}}
